import React from "react";

import Container from '../../components/container';

import styles from './index.module.css';

export default ({ data }) => {
  console.log(data)
  return (
    <div>
      <section className={styles.about}>
        <Container>        
          <h1 className={styles.title}>Hello!<br/> I am <strong>Henrique Craveiro</strong></h1>
          <h2 className={styles.subtitle}>Full stack web developer, amateur photographer and 
adventure sports lover.</h2>
        </Container>
      </section>
      <Container>
        <h1>
          Latest posts
        </h1>
        {data.markdownRemark.frontmatter.latestPosts.map(post => (
          <div key={post.id} className={styles.post}>
            <a href={post.uniqueSlug} target="_blank"><h3>{post.title}{" "}</h3></a>
            <p>{post.subtitle}</p>
            <p><span className={styles.date}> {post.date.slice(0, 10)}</span> - <span className={styles.tags}>{post.tags.join(', ')}</span></p>
          </div>
        ))}
      </Container>
    </div>
  );
};

export const query = graphql`
  query BlogPostQuery {
    markdownRemark(fields: { slug: { eq: "/index/" } }) {
      html
      frontmatter{
        title
        date
        latestPosts {
          title
          tags
          uniqueSlug
          id
          date
          subtitle
          uniqueSlug
        }
      }
    }
  }
`;


